import React, { Fragment, useState, useEffect } from "react";
import { Row, Col, Container, CardGroup, Card, Table } from "react-bootstrap";
import { StaticImage } from "gatsby-plugin-image";

import rock_bg from "../../styles/assets/images/Decentralize_Exchange/blobs.webp";
import new_rocket from "../../styles/assets/images/Decentralize_Exchange/new_rocket.webp";

import Carousel from "./exchange-slider";
import TalkToUs from "../../components/common/talk-to-us";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";

import Loading from "../../components/common/loading";
import { useForm } from "react-hook-form";
import { Country } from "country-state-city";
import { collection, addDoc } from "firebase/firestore/lite";
import { utils } from "../../core/helper";
import Faq from "../../components/common/faq";
import { Link } from "gatsby";
import CtaButton from "../../components/common/contactbutton";
import MoveForward from "../../components/common/move-forward";
import ServicesStatsOfArt from "../../components/common/service-stats-of-art";
import ConnectButton from "../../components/common/connect-button";
import TalkToUsNew from "../../components/common/talk-to-us-new";

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Contact is a required field"),
  service: Yup.string().label("service").required(),
  content: Yup.string().label("Content").required(),
});

const DecentralizedExchangeService = (props) => {
  const { db } = props;
  const [loading, setLoading] = useState(false);
  const [talkToUsShow, setTalkToUsShow] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const handleToggleTalkToUs = () => {
    setTalkToUsShow(!talkToUsShow);
    reset();
  };

  const countries = Country.getAllCountries();

  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile, service } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      service,
      date: new Date(),
    };
    setLoading(true);
    try {
      const clients = collection(db, "clients");
      await addDoc(clients, payload);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
      setTimeout(() => {
        handleToggleTalkToUs();
      }, 1000);
    } catch (e) {
      utils.showErrMsg("Please enter the valid details");
      setLoading(false);
    }
  };

  const [addClass, setAddClass] = useState(false);
  const [addContainerClass, setAddContainerClass] = useState(false);

  const toggleClass = () => {
    setAddClass(true);
    setAddContainerClass(true);
  };

  const [buttonClicked, setButtonClicked] = useState(false);
  const [showButton, setShowButton] = useState(true);

  const handleBtnClick = () => {
    if (!buttonClicked) {
      setButtonClicked(true);
      setShowButton(false);
      toggleClass();
    }
  };

  useEffect(() => {
    if (buttonClicked) {
      const animationTimeout = setTimeout(() => {
        setShowButton(true);
      });
      return () => clearTimeout(animationTimeout);
    }
  }, [buttonClicked]);

  const rightSideStyle = buttonClicked
    ? { backgroundColor: "#7534FC", color: "#fff", border: "1px solid #fff" }
    : {};

  const blinkAnimationStyle = buttonClicked
    ? { display: showButton ? "block" : "none" }
    : { display: "none" };

  const decFaqData = [
    {
      question: "How secure is your exchange platform?",
      answer:
        "Employing robust security measures, including encryption and two-factor authentication.",
    },
    {
      question: "What cryptocurrencies does your exchange support?",
      answer:
        "Offers support for a variety of cryptocurrencies, including major ones.",
    },
    {
      question: "Is mobile trading supported on your platform?",
      answer:
        "Yes, our platform is optimized for seamless mobile trading experiences.",
    },
    {
      question: "How do you ensure user privacy and compliance?",
      answer:
        "Prioritizing user privacy with stringent KYC and AML procedures.",
    },
  ];

  return (
    <Fragment>
      <div className="decenter_exc_serv_page">
        <section className="decenter_exc_hero_sec_wrapper">
          <Container>
            <Row>
              <Col
                lg={7}
                md={12}
                className="d-flex align-items-center justify-content-center"
              >
                <div className="decenter_exc_hero_sec_lhs ">
                  <div className="decenter_hero_sec_title">
                    <h1>Decentralized Exchange Development Company</h1>
                  </div>
                  <div className="decenter_hero_sec_description">
                    <p>
                      Conquer the boundless world of DeFi by leveraging our
                      exemplary decentralized exchange development services.
                      Create a DEX as unique as your vision by partnering with
                      Justtry today!
                    </p>
                  </div>
                  <div className="decenter_hero_sec_btn">
                    <button
                      type="submit"
                      className="demo_btn"
                      onClick={handleToggleTalkToUs}
                    >
                      View Live Demo
                    </button>
                    <button
                      type="submit"
                      className="consult_btn"
                      onClick={handleToggleTalkToUs}
                    >
                      Free Consult
                    </button>
                  </div>
                </div>
              </Col>
              <Col
                lg={5}
                md={12}
                className="d-flex align-items-center justify-content-center"
              >
                <div className="decenter_exc_hero_sec_rhs ">
                  <div>
                    <StaticImage
                      src="../../styles/assets/images/Decentralize_Exchange/decentr_exc_bitcoin.webp"
                      alt="Bitcoin"
                    />
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </section>
        <section className="decenter_exc_sec_intro_wrapper">
          <Container>
            <Row>
              <Col md={12}>
                <div className="decenter_exc_sec_intro">
                  <h2>
                    Decentralized Exchange Development: <br />
                    Steering Towards the Future
                  </h2>
                  <p>
                    DeFi, short for decentralized finance, has been sending
                    ripples through the financial world, introducing innovation
                    and challenging the traditional financial landscape. In the
                    vast DeFi world, there are many financial tools that are
                    reshaping traditional finance. Decentralized exchanges are
                    leading the way, changing how we trade and manage digital
                    assets. The global Decentralized Finance (DeFi) market is
                    anticipated to attain a substantial size, reaching
                    approximately USD 601.00 billion by the year 2032. Today,
                    there are a multitude of decentralized exchanges, each
                    offering a distinctive set of features and functionalities
                    to engage and retain their user base.{" "}
                  </p>
                  <p>
                    According to a research report by Citigroup, decentralized
                    cryptocurrency exchanges (DEXs) have exhibited faster growth
                    compared to centralized exchanges (CEXs) in the past two
                    years. This trend is expected to continue as users
                    increasingly migrate from centralized platforms to avoid the
                    more taxing know-your-customer (KYC) procedures associated
                    with them. This trend is only expected to continue growing
                    in the near future. Many businesses and entrepreneurs have
                    already initiated investments in  decentralized
                    cryptocurrency exchange development. What are you waiting
                    for? Seize the opportunity to make a significant impact in
                    the DeFi world and launch your decentralized exchange today!
                  </p>
                  <ConnectButton justifyContentPosition="connect_center" />
                </div>
              </Col>
            </Row>
          </Container>
        </section>

        <section className="decenter_exc_sec_do_wrapper">
          <Container>
            <Row>
              <Col lg={6} md={12}>
                <div className="decenter_exc_sec_lhs">
                  <h2>
                    How Does a{" "}
                    <span style={{ color: "#7534FC" }}>
                      Decentralized Exchange Work?
                    </span>
                  </h2>
                  <p>
                    Decentralized exchanges, commonly referred to as DEXs,
                    represent peer-to-peer trading platforms where
                    cryptocurrency traders engage in transactions without giving
                    up control of their assets to an intermediary. These
                    transactions are carried out through self-executing codes
                    called smart contracts. At a decentralized cryptocurrency
                    exchange, the process is quite straightforward. You connect
                    your cryptocurrency wallet to the DEX platform. When you're
                    ready to buy or swap crypto assets, you simply state your
                    preferences. The decentralized exchange app provides you
                    with the price, and if it aligns with your expectations, you
                    confirm the transaction. There's no need for logging in,
                    providing personal information, or creating an account.
                  </p>
                </div>
              </Col>
              <Col
                lg={{ span: 5, offset: 1 }}
                md={12}
                className="d-flex align-items-center justify-content-center "
              >
                <div className="decenter_exc_sec_do_rhs">
                  <StaticImage
                    src="../../styles/assets/images/Decentralize_Exchange/decentr_exc_circle.webp"
                    alt="decentralize exchange work structure"
                  />
                </div>
              </Col>
            </Row>
          </Container>
        </section>
        <Container>
          <CtaButton
            db={db}
            dynamicHeaderText="Elevate Your Business to New Heights <br/> with Our Expertise!

"
          />
        </Container>

        <section className="decenter_exc_sec_slider">
          <Container>
            <Row>
              <Col lg={6} md={12}>
                <h2>Types of Decentralized Exchanges We Offer</h2>
              </Col>
            </Row>
            <Row className="decenter_exc_slider_card">
              <Carousel />
            </Row>
          </Container>
        </section>
        <section className="decenter_exc_sec_platform">
          <Container className="decenter_exc_sec_platform_wrapper">
            <Row>
              <Col xxl={6} xl={6} lg={6} md={12}>
                <div className="decenter_exc_sec_platform_lhs">
                  <h2 className="text-white">
                    Why Should You Launch A Decentralized Exchange Platform?
                  </h2>
                  <p>
                    It's a noteworthy fact that even during bullish market
                    conditions, cryptocurrency exchanges continued to be
                    profitable platforms. Decentralized exchanges (DEXs) have
                    been steadily gaining momentum, making investment in such
                    projects a smart move. There are numerous ways to generate
                    revenue through these DeFi platforms. For instance, DEXs can
                    collect trading fees with each successful transaction,
                    providing a consistent income stream. 
                  </p>
                  <p>
                    {" "}
                    When users move their crypto assets from the DEX to their
                    cryptocurrency wallets, withdrawal fees can be levied.
                    Moreover, some cryptocurrency projects may seek to list
                    their tokens on your decentralized exchange application, and
                    you can charge substantial listing fees for such services.
                    The potential for revenue generation in the realm of DEXs is
                    abundant, and there are myriad opportunities to establish a
                    profitable decentralized exchange of your own. So why wait?
                    Get started today and explore the world of decentralized
                    finance!
                  </p>
                  <ConnectButton justifyContentPosition="connect_start" />
                </div>
              </Col>
              <Col
                xxl={6}
                xl={6}
                lg={6}
                md={12}
                className="d-flex align-items-center justify-content-center decenter_exc_rhs_wrapper "
              >
                <div className="decenter_exc_sec_platform_rhs">
                  <img
                    className="static_image"
                    src={new_rocket}
                    alt="Rocket ready to go"
                  />
                </div>
              </Col>
            </Row>
            <img
              className="decenter_exc_bg"
              src={rock_bg}
              alt="rocket background"
            />
          </Container>
        </section>
        <section className="decenter_exc_sec_cards">
          <Container>
            <Row>
              <Col
                md={12}
                className="d-flex align-items-center justify-content-center"
              >
                <h2>Fundamental Features of Our Decentralized Exchange</h2>
              </Col>
            </Row>
            <Row>
              <Col md={12}>
                <div className="decenter_exc_cards_first">
                  <CardGroup>
                    <Card>
                      <Card.Header>Seamless Trading Experience</Card.Header>
                      <Card.Body>
                        <Card.Text>
                          Experience seamless trading on our DEX platform,
                          enabling users to conduct global transactions with
                          minimal latency and zero downtime. Revolutionary DEX:
                          Seamless global transactions, unmatched speed, minimal
                          latency, zero downtime.
                        </Card.Text>
                      </Card.Body>
                    </Card>
                    <Card>
                      <Card.Header>Smart Contract Integration</Card.Header>
                      <Card.Body>
                        <Card.Text>
                          Our skilled DEX experts seamlessly integrate smart
                          contracts into the exchange platform. This integration
                          allows for secure and automated transactions,
                          enhancing efficiency and accuracy while safeguarding
                          user data privacy.
                        </Card.Text>
                      </Card.Body>
                    </Card>
                    <Card>
                      <Card.Header>
                        Intuitive User <br /> Interface
                      </Card.Header>
                      <Card.Body>
                        <Card.Text>
                          Our decentralized exchange platforms boast an
                          intuitive user interface equipped with cutting-edge
                          features. This ensures a glitch and error-free
                          experience for every user, making it a perfect fit for
                          both beginners and experienced traders.
                        </Card.Text>
                      </Card.Body>
                    </Card>
                    <Card>
                      <Card.Header>
                        Transaction <br />
                        History
                      </Card.Header>
                      <Card.Body>
                        <Card.Text>
                          Every transaction, whether it's a withdrawal or
                          deposit, is meticulously recorded, providing users
                          with an accessible transaction history. This
                          transparency allows users to stay informed about all
                          activities on the exchange platform.
                        </Card.Text>
                      </Card.Body>
                    </Card>
                  </CardGroup>
                </div>
              </Col>
              <Col md={12}>
                <div className="decenter_sec_cards_second">
                  <CardGroup>
                    <Card>
                      <Card.Header>Multi-Cryptocurrency Trading</Card.Header>
                      <Card.Body>
                        <Card.Text>
                          Leverage our decentralized exchange development
                          services to empower users with seamless
                          multi-cryptocurrency trading on your newly launched
                          DEX platform. Our compatibility with popular wallets
                          like Exodus, Electrum, Mycelium, and others enhances
                          the overall user experience.
                        </Card.Text>
                      </Card.Body>
                    </Card>
                    <Card>
                      <Card.Header>
                        Multi-Layer <br />
                        Security
                      </Card.Header>
                      <Card.Body>
                        <Card.Text>
                          Our P2P platform places a strong emphasis on security,
                          implementing two-factor authentication to shield
                          users' data and transactions. This commitment ensures
                          a secure and reliable trading environment, fostering
                          trust and confidence among our users.
                        </Card.Text>
                      </Card.Body>
                    </Card>
                    <Card>
                      <Card.Header>Automated Market Makers</Card.Header>
                      <Card.Body>
                        <Card.Text>
                          Our DEX platform employs the Automated Market Maker
                          (AMM) protocol, enabling transactions without the
                          involvement of third parties. This protocol utilizes a
                          mathematical formula that considers current liquidity
                          pools for cryptocurrency tokens, delivering instant
                          auto-quotes to traders.
                        </Card.Text>
                      </Card.Body>
                    </Card>
                    <Card>
                      <Card.Header>Admin and User Dashboards</Card.Header>
                      <Card.Body>
                        <Card.Text>
                          Our DEX platform features intuitive admin and user
                          dashboards, providing exchange owners and traders with
                          user-friendly tools for efficient operation
                          management, eliminating the need for specialized
                          programming skills.
                        </Card.Text>
                      </Card.Body>
                    </Card>
                  </CardGroup>
                </div>
              </Col>
            </Row>
          </Container>
        </section>
        <ServicesStatsOfArt />

        <CtaButton db={db} />

        <section className="decenter_exc_sec_cards">
          <Container
            className={` ${
              addContainerClass
                ? "removeContainer"
                : "decenter_exc_sec_contact_wrapper"
            }`}
          >
            <Row>
              <Col md={12}>
                <h2 className="text-center">
                  Why Choose Justtry for Decentralized Exchange Platform
                  Development?
                </h2>
              </Col>
            </Row>
            <Row className="decenter_exc_sec_justtry">
              <Col xl={6} lg={12} md={12}>
                <div className="decenter_exc_sec_justtry_wrapper">
                  <div className="decenter_exc_sec_justtry_content">
                    <p>
                      At Justtry Technologies, we build your dream project with
                      expertise, innovation, and an unwavering commitment to
                      your success. We have an adept team of seasoned experts in
                      the realm of decentralized exchange development, all
                      equipped with an extensive understanding of the latest
                      trends and cutting-edge technologies in the ever-evolving
                      blockchain and DeFi landscape. Built upon a foundation of
                      successful projects and delighted clients within the DeFi
                      sector, we provide concrete proof of our ability to
                      consistently deliver outstanding results.
                    </p>
                  </div>
                </div>
              </Col>
              <Col xl={6} lg={12} md={12}>
                <div className="decenter_exc_sec_contact">
                  <Table bordered responsive style={rightSideStyle}>
                    <tbody>
                      <tr style={rightSideStyle}>
                        <td style={rightSideStyle}>
                          Transparent Communication
                        </td>
                        <td style={rightSideStyle}>Proven Track Record</td>
                        <td style={rightSideStyle}>User-Centric Design</td>
                      </tr>
                      <tr style={rightSideStyle}>
                        <td style={rightSideStyle}>
                          Quality <br />
                          Assurance
                        </td>
                        <td style={rightSideStyle}>
                          Timely <br /> Delivery
                        </td>
                        <td style={rightSideStyle}>Round-The-Clock Support</td>
                      </tr>
                    </tbody>
                  </Table>

                  <div className="crypto_btns_grp justify-content-start">
                    <Link className="prime_submit_btn" to="/contact-us">
                      View Live Demo
                    </Link>
                    <button className="second_consult_btn" type="button">
                      <TalkToUsNew label="Free Consult" db={db} />
                    </button>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </section>
        <Faq faqData={decFaqData} />
        <Container>
          <MoveForward db={db} />
        </Container>

        <TalkToUs
          show={talkToUsShow}
          onHide={handleToggleTalkToUs}
          closeButton={true}
          size={"lg"}
        >
          <div className="screen__content">
            <form onSubmit={handleSubmit(handleOnSubmit)} className="login">
              <div className="login__field">
                <i className="login__icon fas fa-user"></i>
                <input
                  type="text"
                  className="login__input"
                  {...register("name")}
                  placeholder="Your Name"
                />
                <p className="text-danger mb-0">{errors.name?.message}</p>
              </div>
              <div className="login__field">
                <input
                  type="email"
                  className="login__input"
                  {...register("email")}
                  placeholder="Email"
                />
                <p className="text-danger mb-0">{errors.email?.message}</p>
              </div>
              <div className="login__field">
                <input
                  type="text"
                  className="login__input"
                  placeholder="Skype / Mobile"
                  {...register("mobile")}
                />
                <p className="text-danger mb-0">{errors.mobile?.message}</p>
              </div>
              <div className="login__field">
                <select className="login__input" {...register("country")}>
                  <option value="">Select</option>
                  {countries.map((ele, ind) => {
                    return (
                      <option key={ind} value={ele.isoCode}>
                        {ele.name}
                      </option>
                    );
                  })}
                </select>
                <p className="text-danger mb-0">{errors.country?.message}</p>
              </div>
              <div className="login__field">
                <select className="login__input" {...register("service")}>
                  <option value="">Select your services</option>
                  <option value="crypto exchange development">
                    Crypto Exchange Development
                  </option>
                  <option value="nft marketplace development">
                    NFT Marketplace Development
                  </option>
                  <option value="defi development">DeFi Development</option>
                  <option value="metaverse development">
                    Metaverse Development
                  </option>
                  <option value="blockchain solutions">
                    Blockchain Solutions
                  </option>
                  <option value="web 3.0 development">
                    Web 3.0 Development
                  </option>
                  <option value="crypto wallet development">
                    Crypto Wallet Development
                  </option>
                  <option value="custom software development">
                    Custom Software Development
                  </option>
                  <option value="mobile app development services">
                    Mobile App Development Services
                  </option>
                  <option value="artificial intelligence solutions">
                    Artificial Intelligence Solutions
                  </option>
                  <option value="others"> Others</option>
                </select>
                <p className="text-danger mb-0">{errors.service?.message}</p>
              </div>
              <div className="login__field">
                <textarea
                  id="content"
                  placeholder="Type content here"
                  rows={5}
                  className="login__input"
                  {...register("content")}
                  maxLength={200}
                ></textarea>
                <p className="text-danger mb-0">{errors.content?.message}</p>
              </div>
              <button
                type="submit"
                disabled={loading}
                className="button login__submit"
              >
                <span className="button__text">
                  {" "}
                  Submit {loading && <Loading />}
                </span>
                <i className="button__icon fas fa-chevron-right"></i>
              </button>
            </form>
          </div>
          <div className="screen__background">
            <span className="screen__background__shape screen__background__shape4"></span>
            <span className="screen__background__shape screen__background__shape3"></span>
            <span className="screen__background__shape screen__background__shape2"></span>
            <span className="screen__background__shape screen__background__shape1"></span>
          </div>
        </TalkToUs>
      </div>
    </Fragment>
  );
};

export default DecentralizedExchangeService;
